.page {
  /* size: A4; */
  /* padding: 6mm; */
  padding-top: 25px;
  padding-bottom: 25px;
  /* border: 1px #D3D3D3 solid; */
  /* border-radius: 5px; */
  background: white;
  /* box-shadow: 0 0 5px rgba(0, 0, 0, 0.1); */
  text-align: center;
  height: 280mm;
  display: flex;
  flex-direction: column;
}
.image {
  width: 26px;
  height: 26px;
}
.page-header {
  /* background-color: #282c34; */
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-around;
  font-size: 22px;
  margin-top:  20px;
  margin-bottom:  20px;
}
.controllers {
  flex-direction: row;
  display: flex;
  justify-content: space-around;
  align-items: center;
  margin: 10px;
  background-color: lightblue;
  padding: 20px;
  flex-wrap:wrap;
}
.content {
  flex-grow:1;
  /* margin:  5px; */
  font-size: 18px;
  flex-direction: row;
  display: flex;
  justify-content: space-around;
 
}

.group {
  flex-direction: column;
  display: flex;
  flex-grow: 1;
  justify-content: space-between;
}


.item {
  /* margin: 19px; */
}

.item:last-child {
  margin-bottom: 0;
}
